import React from 'react';
import './ConfirmDialog.css';

/**
 * Componente para mostrar un dilogo de confirmacin
 * @param {Object} props - Propiedades del componente
 * @param {string} props.title - Ttulo del dilogo
 * @param {string} props.message - Mensaje a mostrar
 * @param {string} props.confirmText - Texto del botn de confirmacin
 * @param {string} props.cancelText - Texto del botn de cancelacin
 * @param {Function} props.onConfirm - Funcin a ejecutar al confirmar
 * @param {Function} props.onCancel - Funcin a ejecutar al cancelar
 * @param {boolean} props.show - Si se debe mostrar el dilogo
 * @param {string} props.type - Tipo de confirmacin ('danger', 'warning', 'info')
 */
function ConfirmDialog({ 
  title = 'Confirmar accin', 
  message, 
  confirmText = 'Confirmar', 
  cancelText = 'Cancelar', 
  onConfirm, 
  onCancel, 
  show = false,
  type = 'warning'
}) {
  if (!show) return null;
  
  const handleConfirm = (e) => {
    e.preventDefault();
    if (onConfirm) onConfirm();
  };
  
  const handleCancel = (e) => {
    e.preventDefault();
    if (onCancel) onCancel();
  };
  
  // Prevenir que los clics en el dilogo cierren el modal
  const handleDialogClick = (e) => {
    e.stopPropagation();
  };
  
  return (
    <div className="confirm-overlay" onClick={handleCancel}>
      <div className={`confirm-dialog confirm-${type}`} onClick={handleDialogClick}>
        <div className="confirm-header">
          <h3 className="confirm-title">{title}</h3>
        </div>
        
        <div className="confirm-content">
          <p className="confirm-message">{message}</p>
        </div>
        
        <div className="confirm-actions">
          <button 
            className="confirm-cancel" 
            onClick={handleCancel}
          >
            {cancelText}
          </button>
          <button 
            className={`confirm-button confirm-${type}-button`} 
            onClick={handleConfirm}
          >
            {confirmText}
          </button>
        </div>
      </div>
    </div>
  );
}

export default ConfirmDialog;