/**
 * Utilidades para manejar peticiones de red y errores
 */

import { logError } from './debug';

/**
 * Funcin para verificar la conectividad a Internet
 * @returns {Promise<boolean>} - Promesa que resuelve a true si hay conexin, false en caso contrario
 */
export const checkInternetConnection = async () => {
  try {
    // Intentar hacer una peticin a un servicio confiable
    const controller = new AbortController();
    const timeoutId = setTimeout(() => controller.abort(), 5000);
    
    const response = await fetch('https://www.google.com', { 
      method: 'HEAD',
      mode: 'no-cors',
      signal: controller.signal
    });
    
    clearTimeout(timeoutId);
    return true;
  } catch (error) {
    logError(error, 'checkInternetConnection');
    return false;
  }
};

/**
 * Funcin para manejar errores de red comunes
 * @param {Error} error - El error capturado
 * @returns {string} - Mensaje de error amigable para el usuario
 */
export const getNetworkErrorMessage = (error) => {
  // Verificar si es un error de timeout
  if (error.name === 'AbortError') {
    return 'La conexin ha tardado demasiado tiempo. Por favor, verifica tu conexin a Internet.';
  }
  
  // Verificar si es un error de red
  if (error.message && error.message.includes('NetworkError')) {
    return 'No se ha podido conectar con el servidor. Por favor, verifica tu conexin a Internet.';
  }
  
  // Verificar si es un error CORS
  if (error.message && error.message.includes('CORS')) {
    return 'Error de acceso al servidor. Por favor, contacta al administrador.';
  }
  
  // Verificar si es un error de DNS
  if (error.message && error.message.includes('DNS')) {
    return 'No se ha podido encontrar el servidor. Por favor, verifica la URL o tu conexin a Internet.';
  }
  
  // Mensaje genrico para otros errores
  return 'Ha ocurrido un error de conexin. Por favor, intenta de nuevo ms tarde.';
};

/**
 * Funcin para reintentar una peticin fallida
 * @param {Function} fetchFunction - Funcin que realiza la peticin
 * @param {number} maxRetries - Nmero mximo de reintentos
 * @param {number} delayMs - Tiempo de espera entre reintentos en milisegundos
 * @returns {Promise<any>} - Promesa con el resultado de la peticin
 */
export const retryFetch = async (fetchFunction, maxRetries = 3, delayMs = 1000) => {
  let lastError;
  
  for (let attempt = 0; attempt < maxRetries; attempt++) {
    try {
      return await fetchFunction();
    } catch (error) {
      lastError = error;
      logError(error, `retryFetch_attempt_${attempt + 1}`);
      
      // Esperar antes del siguiente intento
      if (attempt < maxRetries - 1) {
        await new Promise(resolve => setTimeout(resolve, delayMs));
      }
    }
  }
  
  // Si llegamos aqu, todos los intentos han fallado
  throw lastError;
};

/**
 * Funcin para manejar errores HTTP segn el cdigo de estado
 * @param {Response} response - La respuesta HTTP
 * @returns {Promise<Response>} - La respuesta si es exitosa, o lanza un error
 */
export const handleHttpResponse = async (response) => {
  if (!response.ok) {
    let errorMessage;
    
    // Intentar obtener el mensaje de error del cuerpo de la respuesta
    try {
      const errorData = await response.json();
      errorMessage = errorData.error || errorData.message || `Error ${response.status}: ${response.statusText}`;
    } catch (e) {
      // Si no se puede parsear el cuerpo, usar el statusText
      errorMessage = `Error ${response.status}: ${response.statusText}`;
    }
    
    const error = new Error(errorMessage);
    error.status = response.status;
    error.statusText = response.statusText;
    error.response = response;
    
    logError(error, 'handleHttpResponse');
    throw error;
  }
  
  return response;
};